import React from 'react';
import { StyleSheet, css } from 'aphrodite/no-important';
import CheckBox from '../ui-elements/checkbox/CheckBox'
import TextFieldSmall from '../ui-elements/TextFieldSmall'

const styles = StyleSheet.create({
	wrapper: {
		background: '#fff',
		borderRadius: '3px',
		padding: '20px',
	},
	block: {
		padding: '20px 0',
		borderBottom: '1px solid #eaeaea'
	},
	blockTitle: {
		fontSize: '22px',
		fontWeight: 'bold',
		textTransform: 'uppercase',
		margin: '0 0 15px'
	}
});

const SearchSideBar = () =>
	<div className={css(styles.wrapper)}>
		<div className={css(styles.block)}>
			<h3 className={css(styles.blockTitle)}> </h3>
			<TextFieldSmall placeholder=' ' />
		</div>
		<div className={css(styles.block)}>
			<h3 className={css(styles.blockTitle)}></h3>
			<CheckBox label="" name="belarus" block={ true } />
			<CheckBox label="" name="" block={ true } />
			<CheckBox label="" name="" block={ true } />
			<CheckBox label="" name="" block={ true } />
		</div>
		<div className={css(styles.block)}>
			<h3 className={css(styles.blockTitle)}> </h3>
			<CheckBox label="1" name="1" />
			<CheckBox label="2" name="2" />
			<CheckBox label="3" name="3" />
			<CheckBox label="4" name="4" />
			<CheckBox label="5" name="5" />
			<CheckBox label="6" name="6" />
		</div>
		<div className={css(styles.block)}>
			<h3 className={css(styles.blockTitle)}> </h3>
			<CheckBox label="" name="" block={ true } />
			<CheckBox label="" name="" block={ true } />
			<CheckBox label="" name="" block={ true } />
			<CheckBox label="" name="" block={ true } />
		</div>
	</div>

export default SearchSideBar;



